import { isPlatformBrowser, NgOptimizedImage } from '@angular/common';
import { AfterViewInit, Component, Inject, OnInit, PLATFORM_ID } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, FormsModule, ReactiveFormsModule, Validators } from '@angular/forms';
import { Meta, Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { adminLogin, meAdmin } from '@biz-nest/auth-client';
import { FrontendLogger, getBizNestErrorResponseMessage } from '@biz-nest/core-client';
import {
  AppEnvironmentService,
  AppNameService,
  AuthService, CurrencyService, DataService,
  LoaderSpinnerComponent,
  MainBarTitleService
} from '@front-shared-lib';
import { controlNames } from '@main-shared-lib';
import { IDatePickerConfig } from 'ng2-date-picker';
import { ToastrService } from 'ngx-toastr';
import { environment } from '../../../../environments/environment';

interface Ilogin {
  emailOrPhoneNumber: string;
}

@Component({
  selector: 'app-login',
  standalone: true,
  imports: [
    FormsModule,
    ReactiveFormsModule,
    NgOptimizedImage,
    LoaderSpinnerComponent
  ],
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent
implements OnInit, AfterViewInit {
  toggleData = true;
  errMsg?: string;
  form: FormGroup<{
    [K in keyof Ilogin]: FormControl<Ilogin[K] | null>
  }>;

  posting = false;
  date = new Date();
  config: IDatePickerConfig = {
    format: '"DD-MM-YYYY"'
  };

  isChecked = false;

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private dataService: DataService,
    private toastr: ToastrService,
    public auth: AuthService,
    private metaTagService: Meta,
    private titleService: Title,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private currencyService: CurrencyService,
    @Inject(PLATFORM_ID) private platformId: object
  ) {
    this.form = this.fb.group({
      emailOrPhoneNumber: ['', Validators.compose([Validators.minLength(4), Validators.required])]
    });
  }

  get controlNames() {
    return controlNames(this.form.controls);
  }

  ngOnInit(): void {
    const storeCurrency = this.currencyService.getStorageCurrency();

    this.currencyService
      .setCurrency(storeCurrency && (storeCurrency?.length > 0 && storeCurrency?.length < 4) ? storeCurrency : 'USD');
    AppEnvironmentService.environment = {
      production: environment.production
    };
    // this.runControllers();
    FrontendLogger.error('after controller');
    this.mainBarTitle.title = 'Login';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content:
        'Admin panel login page for secure access to administrative features.'
    });
  }

  ngAfterViewInit() {
    if (isPlatformBrowser(this.platformId)) {
      this.getRememberMe();
    }
  }

  async submit() {
    this.posting = true;
    try {
      const emailOrPhoneNumber = String(this.form.get('emailOrPhoneNumber')?.value);
      const isPhone = emailOrPhoneNumber.match(/^[0-9]{10}$/);
      let body: object = {};

      const rememberMeDays = this.isChecked ? 7 : 1;

      if (isPhone) {
        body = {
          phone: emailOrPhoneNumber

        };
      } else {
        body = {
          email: emailOrPhoneNumber
        };
      }

      const response = await adminLogin(body);

      FrontendLogger.debug('Login success', response);
      this.router.navigate(['/verify/' + rememberMeDays]);
    } catch (error) {
      FrontendLogger.error('Login error', error);
      this.toastr.error(getBizNestErrorResponseMessage(error), 'Failed');
    } finally {
      this.posting = false;
    }
  }

  getRememberMe() {
    this.isChecked = this.auth.getRememberMe() === 'true';
    if (this.isChecked) {
      return this.authExprAdmin();
    }
  }

  rememberMe(ev: boolean) {
    this.auth.remeberMe(ev);
  }

  async authExprAdmin() {
    this.posting = true;
    try {
      const response = await meAdmin();

      FrontendLogger.debug('authExprAdmin success', response);

      this.dataService.isSuperAdmin = true;
      this.auth.isLoggedIn = true;
      this.form.reset();

      const storeCurrency = this.currencyService.getStorageCurrency();

      this.currencyService
        .setCurrency(storeCurrency &&
          (storeCurrency?.length > 0 &&
            storeCurrency?.length < 4) ? storeCurrency : response.defaultCurrency || 'UGX');

      this.router.navigate(['/']);
    } catch (error) {
      FrontendLogger.error('authExprAdmin error', error);
    } finally {
      this.posting = false;
    }
  }
}
